
import PsLabel from "../label/PsLabel.vue";

export default {    
    name: "PsRadioValue",
    components: { PsLabel },
    props: {
        "value" : String,
        "title" : {
            type : String,
            default : "N.A"
        }
    },
    setup( _ , {emit}) {
        function handleInput(v) {                
            emit('update:value', v);
        }

        return {
            handleInput
        }
    }
}
